.title1 {
	position: relative;
	margin-bottom: 40px;
	padding-bottom: 25px;
	/* text-transform: uppercase; */
	font-weight: 700;
}

.title1:before {
	content: "";
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	width: 140px;
	height: 3px;
	background-color: #1cabe2;
}

.title1:after {
	content: "";
	position: absolute;
	bottom: -1px;
	left: 50%;
	transform: translateX(-50%);
	border-radius: 2px;
	width: 45px;
	height: 6px;
	background-color: #1cabe2;
}

.title1 span {
	color: #1cabe2;
}



